.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.img
{  
	width:400px;
	height:auto ;
	margin: 5%;
	
	}


@media only screen and (max-width: 600px) and (min-width: 350px) {
	  .img
  {
  	width:300px;
  	height:auto;
    
    border: transparent !important;
  }

  .bounding-box {
 width: auto;
 height: auto;
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
}


@media only screen and (max-width: 349px) and (min-width: 250px) {
  .img
  {
    width:250px;
    height:auto;
  
    border: transparent !important;
  }

  .bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  width: auto;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
 
  }